import React from 'react';
import { connect } from 'react-redux';
import './style.css';

const uxDetail = ({ project }) => {
    if(!project){
        return <div>Select a Project</div>
    }
    if(project.title === 'UX LAB') {
       return (
            <div className="project-details ui text container">
                <h1>UX Lab</h1>
                <p>{project.description}</p>
                <p>{project.descriptionTwo}</p>
                <p>{project.descriptionThree}</p>
                <p>{project.descriptionFour}</p>
            </div>
        );
    }
    if(project.title === 'Donors to Donations') {
        return (
             <div className="project-details ui text container">
                 <h1>{project.title}</h1>
                 <h3>{project.languages}</h3>
                 <p>{project.description}</p>
                 <p>{project.descriptionTwo}</p>
                 <p>{project.descriptionThree}</p> 
                 <button className="ui button theme-orange">
                 <a target="_blank" rel="noopener noreferrer" className="project-link" 
                        href={project.link}>See our Presentation</a>
                </button>
             </div>
         );
     }
    return (
        <div className="project-details ui text container">
            <h1>{project.title}</h1>
            <p>{project.content}</p>
        </div>
    );
};

const mapStateToProps = (state) => {
    return { project: state.uxProjectSelected }
};

export default connect(mapStateToProps)(uxDetail);    